import { Component, ChangeDetectionStrategy, NgZone, ChangeDetectorRef } from "@angular/core";
import { NavController, NavParams, Platform, ViewController, Button, App } from "ionic-angular";
import { Content } from "ionic-angular";

import { ElementRef, ViewChild } from "@angular/core";
import Magnifier from 'magnifier';

import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";

import { MarsPictureService } from "@services/picture.service";
import { MarsInteractionService } from "@services/interaction.service";

import { Backend } from "@backend/index";
import { MarsSocket } from "@app/app.socket";
import { MarsAuthService } from "@services/auth.service";
import { MarsNavigationService } from "@services/navigation.service";


@Component({
    selector: "page-pet-details",
    templateUrl: "pet-details.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class PetDetailsPage {

    navigationService: MarsNavigationService;

    @ViewChild("picture") picture: ElementRef;
    @ViewChild("content") content: Content;

    token: string;
    translations: AppTranslations;
    currentImg = "";
    petId: string;
    pet: any;
    magnifier: any;
    segment: string = "information";

    /* Comments area */
    comments: Array<any>;
    @ViewChild("commentsScroll") commentsScroll: ElementRef;
    @ViewChild("commentInput") commentInput: ElementRef;
    @ViewChild(Button) sendButton: Button;
    newCommentValue: string = "";
    isLoadingComments;

    /* Adoption area */
    @ViewChild("emailToClipboard") emailToClipboard: ElementRef;
    @ViewChild("phoneToClipboard") phoneToClipboard: ElementRef;

    constructor(private app: App,
        private platform: Platform,
        private navCtrl: NavController,
        private navParams: NavParams,
        private viewCtrl: ViewController,
        private zone: NgZone,
        private changeDetector: ChangeDetectorRef,
        private locales: AppLocales,
        private globals: AppGlobals,
        private pictureService: MarsPictureService,
        private interactionService: MarsInteractionService) {
        this.translations = this.locales.load();
        this.navigationService = new MarsNavigationService(this.app);
        this.navigationService.setNavCtrl(this.navCtrl);
    }

    ionViewWillEnter() {
        if (MarsAuthService.isLoggedIn()) this.token = MarsAuthService.getMarsToken();
        this.petId = this.viewCtrl.data ? this.viewCtrl.data.petId : this.navParams.get("petId");
        this.getPetDetails();
    }

    ionViewDidEnter() {
        setTimeout(() => { this.content.scrollTo(0, 200, 200); this.changeDetector.detectChanges(); }, 500);
    }

    async getPetDetails() {
        this.zone.run(async () => {
            try {
                this.pet = (await Backend.getPet({ id: this.petId })).data;
                console.log(this.pet);
                this.currentImg = this.pet.pictures[0];
            } catch (e) {
                this.interactionService.alert(this.translations.server_failure);
            } finally {
                this.changeDetector.detectChanges();
            }
        });
    }

    private atachCommentsSocket() {
        MarsSocket.join(this.pet._id);
        MarsSocket.on("new comment", () => {
            this.getPetDetails();
        });
    }

    private detachCommentsSocket() {
        MarsSocket.leave(this.pet._id);
    }

    private onKeyPress(event: KeyboardEvent) {
        let userPressedEnter = event && event.keyCode && (event.keyCode == 13);
        if (userPressedEnter && this.sendButton) {
            this.sendButton.getNativeElement().click();
            event.preventDefault();
        }
    }

    private createComment(content) {
        if (MarsAuthService.isLoggedIn()) {
            this.sendComment({ content: content });
            this.clearCommentInput();
        } else {
            this.interactionService.alert(this.translations.whoops_you_must_be_logged_in_to_perform_this_action, () => {
                this.globals.isPlacingOrder = true;
                this.navigationService.setRoot('LoginPage', { token: 'access' });
            })
        }
    }

    private async sendComment(comment) {
        try {
            this.isLoadingComments = true;
            this.pet.comments = (await Backend.addCommentToPet({ xAccessToken: this.token, comment: comment, id: this.pet._id })).data;
        } catch (e) {
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.scrollCommentsDown();
            this.isLoadingComments = false;
            this.changeDetector.detectChanges();
        }
    }

    private clearCommentInput() {
        this.commentInput.nativeElement.value = this.newCommentValue = "";
    }

    private isSent(comment) {
        return MarsAuthService.isLoggedIn() && comment && comment.user && (comment.user._id == MarsAuthService.getLoggedInUser()._id);
    }

    private scrollCommentsDown() {
        setTimeout(() => { if (this.commentsScroll) this.commentsScroll.nativeElement.scrollTop = this.commentsScroll.nativeElement.scrollHeight; }, 100);
    }

    private selectSegment(segment) {
        this.segment = segment;
        setTimeout(() => { this.changeDetector.detectChanges(); }, 100);
    }

    private sendEmail() {
        this.copyToClipboard("emailToClipboard");
    }

    private sendMessage() {
        this.copyToClipboard("phoneToClipboard");
    }

    private copyToClipboard(id: string) {
        /* Get the text field */
        let textElement = this[id].nativeElement;

        /* Select the text field */
        textElement.select();

        /* Copy the text inside the text field */
        document.execCommand("copy");

        /* Alert the copied text */
        this.interactionService.toast(this.translations.copied_to_the_clipboard);
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }
}



